import { Component, OnInit, Inject, ElementRef, ViewChild } from '@angular/core';
import { FormGroup, FormBuilder, Validators, ValidatorFn,AbstractControl} from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MtxDialogData } from '@ng-matero/extensions';
import {  FormControl, FormArray } from '@angular/forms';
import { MasterService } from 'app/shared/services/master/master.service';
import { Router } from '@angular/router';
import { PATTERNS } from 'app/shared/utils/validation-patterns';
import {Observable} from 'rxjs';
import {PaymentService} from 'app/shared/services/payment/payment.service'
import {map, startWith,tap} from 'rxjs/operators';
import {MessageConstant} from 'app/shared/utils/message.constant';
import {ViewEncapsulation} from '@angular/core';
import { fuseAnimations } from '@fuse/animations';

export class PaymentModel {
  id : number;
  id_tag : number;
  user_name_start: string;
  order_id :number;
  vehicle_number : number;
  mobile : number ;
  charger_display_id: number;
  connector_no : number;
  station_name: string;
  energy_consumed: any;
  duration: number;
  payable_amount: number;
  status :string;
}
function autocompleteObjectValidator(): ValidatorFn {
  return (control: AbstractControl): { [key: string]: any } | null => {
    if (typeof control.value === 'string') {
      return { 'invalidAutocompleteObject': { value: control.value } }
    }
    return null  /* valid option selected */
  }
}

@Component({
  selector: 'app-view-dialog',
  templateUrl: './view-dialog.component.html',
  styleUrls: ['./view-dialog.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations   : fuseAnimations
})
export class ViewDialogComponent implements OnInit {

  params: any;
  isEditMode = false;
  form: FormGroup;
  localStorage : any;
  status = false;

  @ViewChild("toggleElement") ref: any;
  @ViewChild("activeElement") active:any;
 // messageConstant = MessageConstant;

  constructor(private fb: FormBuilder,private snackBar: MatSnackBar,public dialogRef: MatDialogRef<ViewDialogComponent>, 
                @Inject(MAT_DIALOG_DATA) public data: MtxDialogData, private masterService: MasterService,private paymentService : PaymentService ,
                private router : Router) {

                }

  ngOnInit() {

    this.initialize();
  }

  initialize(){

    this.localStorage = JSON.parse( localStorage.getItem('userDetails'));
    this.params = this.data;
    this.isEditMode = this.params.action == 'CREATE' ? false : true;
    this.localStorage = JSON.parse( localStorage.getItem('userDetails'));

    this.form = this.fb.group({
      user_name_start: ['', [Validators.required]],
      order_id: ['', [Validators.required]],
      vehicle_number: ['', [Validators.required]],
      mobile: ['',[Validators.required]],
      charger_display_id: ['', [Validators.required]],
      connector_no: ['', [Validators.required]],
      station_name: ['',[Validators.required]],
      energy_consumed: ['', [Validators.required]],
      duration: ['', [Validators.required]],
      id_tag: ['', [Validators.required]],
      payable_amount: ['',[Validators.required]],
     
    });

    if (this.isEditMode){
      this.assignValues(this.params);
    }
  
  }

  close() {
    this.dialogRef.close();
  }

  closeActionPanel(){
    this.dialogRef.close();
  }

  getErrorMessage(form: FormGroup) {
    return form.get('email').hasError('required')
      ? 'You must enter a value'
      : form.get('email').hasError('email')
      ? 'Not a valid email'
      : '';
  }

  assignValues(params){
    this.form.get('user_name_start').setValue(params.data.user_name_start);
    this.form.get('order_id').setValue(params.data.order_id);
    this.form.get('vehicle_number').setValue(params.data.vehicle_number);
    this.form.get('mobile').setValue(params.data.mobile);
    this.form.get('charger_display_id').setValue(params.data.charger_display_id);
    this.form.get('connector_no').setValue(params.data.connector_no);
    this.form.get('station_name').setValue(params.data.station_name);
    this.form.get('energy_consumed').setValue(params.data.energy_consumed);
    this.form.get('duration').setValue(params.data.duration);
    this.form.get('id_tag').setValue(params.data.id_tag);
    this.form.get('payable_amount').setValue(params.data.payable_amount);
    this.status = params.data.status == 'Y' ? true : false;
  }
  
  updateStatus(event){
    this.status = event.checked;
    this.active.checked = this.status;
  }
  
}










