import { Injectable } from '@angular/core';
import {environment} from '../../../../environments/environment';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class PaymentService {
  apiUrl2 = `${environment.API_URL2}/order` ;
  apiUrl1 = `${environment.API_URL2}/payment` ;
  apiUrl3 = `${environment.API_URL2}/usermanagement` ;
  apiUrl = `${environment.API_URL2}`;
  constructor(private http : HttpClient) { }

  getpendingtransactionSW(params){
    return this.http.post(`${this.apiUrl2}/getpendingtransactionSW/`,params);
  }

  processOffline(params){
    return this.http.post(`${this.apiUrl1}/ProcessOffline/`,params);
    }

  // getusertransactionsummary(user_id){
  //   return this.http.post(`${this.apiUrl2}/getusertransactionsummary/`,user_id);
  // }

  chargexInitiateRefund(params){
    return this.http.post(`${this.apiUrl}/refund/chargexInitiateRefund/`,params);
  }
  

  
}
